#movies img,
#movie img {
  width: 100%;
}
.brand-text{
  font-size: 2rem;
}

#react-logo{
  color: #00d8ff;
}
#imdb-logo{
  color: #f5de50;
}

@media (min-width: 950px){
  #movies .col-md-3 .well {
    height: 390px;
  }
  #movies .col-md-3 img {
    height: 240px;
  }
}


@media only screen and (max-width: 411px) and (max-height: 731px)  {
  .navbar-header{
  width: 100% !important;
  margin: 0px !important;
  text-align: center;
}
.navbar-nav{
  margin: 0px 0px !important;
  width: 100% !important;
  text-align: center;
}
.display-4{
  font-size: 1.6rem !important;
}
.h-100 {
  height: 100%!important;
  width: 262px;
  margin: auto;
}
.jumbotron-fluid {
  padding-right: 0;
  padding-left: 0;
  border-radius: 0;
  width: 383px !important;
}
.col-md-4{
width: 120px !important;
}
.card img{
width: 210px !important;
margin: auto;
}
.footer{
width:411px !important;
}
.navbar{
width:411px !important;
}
}

